import _tool from '@/util/tools.js';
export default {
	data() {
		return {
			DataList: [],
		}
	},
	onLoad() {
		//this.GetBookList();
	},
	onShow() {
		this.GetBookList();
	},
	methods: {
		GetBookList() {
			_tool.GetBookListData().then((res) => {
				if(res == "" || res == null || res == undefined) {
					this.DataList = [];
				} else {
					this.DataList = res;
				}
			});
		},
		editClick() {
			uni.navigateTo({
				url: '/pages/bookshelf/edit'
			})
		},
		readClick(bookData) {
			try {
			    uni.setStorageSync('BookReadData', bookData);
				uni.navigateTo({
					url: '/pages/bookshelf/read'
				})
			} catch (e) {
			    uni.showToast({
			        title: '数据异常',
			        icon: 'none'
			    });
			}
		}
	}
}
